
import React from 'react';

import pdf from './resume.pdf';
import styled from 'styled-components'


const ResumeDownloadButton = styled.a`
    text-decoration: none;
    color:#c4c4c4;
    font-weight: 500;
    font-size: 16px;
    &:focus{
     
      color: #f0860c;
    }
   
`

const Btns =styled.button`


min-width: 118px;
margin-top: 10px;
border-radius: 12px;
background-color: rgba(27,27,27,.91);


 
    border  :none;
    height: 50px;
    width: 49%;
    
    font-size: 80%;
    @media (max-width: 600px) {
      width: 100%;
      
  }
   
`
const ButtonWrapper = styled.div`
    width: 86%;
    display: flex;
    margin: auto;
   
   
    justify-content: space-between;
    gap:5%;
    /* margin: auto; */
    @media (max-width: 600px) {
    flex-direction: column;
    gap:10px;
    margin: auto;
  }
`

const ButtonsWrap = styled.div`
  width:50%;
  margin: auto;
  
  

`
export default function DownloadResume() {
  
  return (
    <ButtonsWrap>
      <ButtonWrapper>
          <Btns>
      <ResumeDownloadButton href={pdf} download> Download</ResumeDownloadButton>
      </Btns>
      <Btns>
      <ResumeDownloadButton href={pdf} target="_blank">View</ResumeDownloadButton>
      </Btns>
    </ButtonWrapper>
    </ButtonsWrap>
    
  );
}
