import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import url_Kamal from './kamal.png'


const useStyles = makeStyles((theme) => ({
  root: {
      width:'50%',
      margin: "auto",
     
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
 
  large: {
    width: "100%",
    
  
  },
}));

export default function KamalImage() {
  const classes = useStyles();


  return (
    <div className={classes.root}>

        <img src={url_Kamal} className={classes.large} alt="Not Available"/>

        
      
     
    </div>
  );
}